div.elem-slide{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  

  /* column-gap: 20px; */
  row-gap: 20px
}

.my-node-enter {
  opacity: 0;
}
.my-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.my-node-exit {
  opacity: 1;
}
.my-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}